

import { useEffect, useState } from "react";
import styled from "styled-components";
import Countdown from "react-countdown";
import { Button, CircularProgress, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";

import './home.css'


import img_hero from "./img/m-04.png";
import img_lineup from "./img/lineup.png";
import img_about from "./img/m-03.png";

import img_itm1 from "./img/m-09.png";
import img_itm2 from "./img/m-10.png";
import img_itm3 from "./img/m-11.png";

import img_discord from "./img/discord.png";
import img_twitter from "./img/twitter.png";
import img_solana from "./img/solana-round.png";


import img_nextdrop from "./img/nextdrop.png";
import img_nftcalendar from "./img/nftcalendar.png";
import img_nftdroplist from "./img/nftdroplist.png";
import img_nonfungi from "./img/nonfungi.png";





import * as anchor from "@project-serum/anchor";

import { LAMPORTS_PER_SOL } from "@solana/web3.js";

import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { WalletDialogButton } from "@solana/wallet-adapter-material-ui";

import {
  CandyMachine,
  awaitTransactionSignatureConfirmation,
  getCandyMachineState,
  mintOneToken,
  shortenAddress,
} from "./candy-machine";

const ConnectButton = styled(WalletDialogButton)``;

const CounterText = styled.span``; // add your styles here
/* eslint-disable */
declare global {
  interface Window {
    dataLayer?: any;
  }
}
/* eslint-enable */
export interface HomeProps {
  candyMachineId: anchor.web3.PublicKey;
  config: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  startDate: number;
  treasury: anchor.web3.PublicKey;
  txTimeout: number;
}

const Home = (props: HomeProps) => {
  const [balance, setBalance] = useState<number>();
  const [isActive, setIsActive] = useState(false); // true when countdown completes
  const [isSoldOut, setIsSoldOut] = useState(false); // true when items remaining is zero
  const [isMinting, setIsMinting] = useState(false); // true when user got to press MINT

  const [itemsAvailable, setItemsAvailable] = useState(0);
  const [itemsRedeemed, setItemsRedeemed] = useState(0);
  const [itemsRemaining, setItemsRemaining] = useState(0);
  
  const [price, setPrice] = useState<string>('-');

  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: "",
    severity: undefined,
  });

  const [startDate, setStartDate] = useState(new Date(props.startDate));

  const wallet = useAnchorWallet();
  const [candyMachine, setCandyMachine] = useState<CandyMachine>();

  const refreshCandyMachineState = () => {
    (async () => {
      if (!wallet) return;

      const {
        candyMachine,
        goLiveDate,
        itemsAvailable,
        itemsRemaining,
        itemsRedeemed,
        price,
      } = await getCandyMachineState(
        wallet as anchor.Wallet,
        props.candyMachineId,
        props.connection
      );
      
      // console.log('refreshCandyMachineState', itemsAvailable, itemsRemaining, (itemsRemaining === 0), price, goLiveDate, candyMachine)
      console.log('goLiveDate', goLiveDate, itemsRemaining)
      setItemsAvailable(itemsAvailable);
      setItemsRemaining(itemsRemaining);
      setItemsRedeemed(itemsRedeemed);
      setPrice(price?.toFixed(10)+'' || '-');

      setIsSoldOut(itemsRemaining === 0);
      setStartDate(goLiveDate);
      setCandyMachine(candyMachine);
      
    })();
  };

  
  const testDatalayer = () => {
    window.dataLayer = window.dataLayer || [];
    window.dataLayer.push({
      'event': 'mint',
      'message': 'test',
    });
    console.log('testDatalayer:', window.dataLayer)
  }

  const onMint = async () => {
    try {
      setIsMinting(true);
      if (wallet && candyMachine?.program) {
        const mintTxId = await mintOneToken(
          candyMachine,
          props.config,
          wallet.publicKey,
          props.treasury
        );

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          props.txTimeout,
          props.connection,
          "singleGossip",
          false
        );

        if (!status?.err) {
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
        } else {
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error: any) {
      // TODO: blech:
      let message = error.msg || "Minting failed! Please try again!";
      if (!error.msg) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          setIsSoldOut(true);
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        }
      }

      setAlertState({
        open: true,
        message,
        severity: "error",
      });
      
    } finally {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
      setIsMinting(false);
      refreshCandyMachineState();
    }
  };

  useEffect(() => {
    (async () => {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
    })();
  }, [wallet, props.connection]);

  useEffect(refreshCandyMachineState, [
    wallet,
    props.candyMachineId,
    props.connection,
  ]);

  const mintcontainerClass = wallet ? 'mint connected' : 'mint';


  return (
    <div className="container" style={{backgroundImage: `url(${img_hero})`}}>

    <div className="wrap">

      <section className="hero" >
        <div className="cta">
          <div className="hl">
            The Lambo<br />
            Diamond Hands Club
          </div>
          <div className="cp">            
            Own 1 of a kind PIXEL LAMBOS.<br />Each LAMBO is unique and there doesn't exist another like it.
          </div>

          <div className="somelinks">
            <a href="https://discord.gg/DuxthFgF6q"><div style={{backgroundImage: `url(${img_discord})`}} /></a>
            <a href="https://twitter.com/PixeLambo"><div style={{backgroundImage: `url(${img_twitter})`}} /></a>
          </div>

        </div>    
      </section>

      <div className={mintcontainerClass}>

          {/* <MintContainer>
            {!wallet ? (
              <ConnectButton fullWidth={false} className="mintbtn">Connect Wallet</ConnectButton>
            ) : (
              <MintButton
                disabled={isSoldOut || isMinting || !isActive}
                onClick={onMint}
                variant="contained"
                className="mintbtn"
                style={{width:'50%'}}
              >
                {isSoldOut 
                ? ("SOLD OUT")
                : isActive 
                  ? (isMinting 
                      ? (<CircularProgress />)
                      : ("MINT"))
                  : ("MINT")
                }
              </MintButton>
            )}
          </MintContainer> */}

          <div>
            {/* <Button className="mintbtn" onClick={testDatalayer}>testDatalayer</Button> */}

            {!wallet && isActive && (<ConnectButton fullWidth={false} className="mintbtn">Connect Wallet</ConnectButton>)}

            {wallet && !isActive && (<Button className="passivebtn">not active</Button>) }

            {wallet && isSoldOut && (<Button className="passivebtn">sold out - reload page?</Button>) }

            {wallet && isMinting && (<Button><CircularProgress /></Button>) }
            
            {wallet && isActive && !isSoldOut && !isMinting && (<Button className="mintbtn" onClick={onMint}>MINT ONE LAMBO</Button>) }
          </div>

          {!isActive && (
            <div>
              <Countdown
                date={startDate}
                onMount={({ completed }) => completed && setIsActive(true)}
                onComplete={() => setIsActive(true)}
                renderer={renderCounter}
              />
              <div className="startdate">{startDate.toUTCString()}</div>
            </div>
          )}

          <div className="price">
          {wallet && (
            <span>Minting price: {price.replace(/0+$/g, '')} SOL <span style={{backgroundImage: `url(${img_solana})`}} className="solana" /></span>
          )}
          </div>

          
      </div>

                  
      {/* <!-- mint here --> */}

</div>
        <div className="lineup" style={{backgroundImage: `url(${img_about})`}}>
          <img src={img_about} alt="" className="icon" />
        </div>

<div className="wrap-blk">
<div className="wrap">

      <div className="content">


        <section className="about">
          <div>
            <div className="hl">About</div>

            <span className="aboutp">
              <p>
                Incredibly awesome hand drawn Pixel Lambos, drivers and backgrounds all come together in unique 1 off custom art work.
              </p>
              <p>
                We are only releasing 3333 Lambos out into the world for mint so they are going to be ultra exclusive. We are then giving away upto a 1000 free Lambos to lucky holders! So there’s a pretty high chance you come out of this with 2 ULTRA AWESOME AND RARE LAMBOS!
              </p>
              <p>
                We are releasing the Lambos in small batches to make this experience more fun!<br />
                Each batch has unique and rare Lambos!
              </p>
            </span>
            
          </div>
        </section>

        <section className="itemcontainer">
          
            <div className="item">
              <div><img className="itemimg" src={img_itm1} alt="" /></div>
              <div className="itemc">
                <strong>All Lambos</strong>
                <p>18 production Lambos from 1964 to 2021. Every single one painstakingly drawn and hand crafted to perfection</p>
              </div>
            </div>

            <div className="item">
              <div><img className="itemimg" src={img_itm2} alt="" /></div>
              <div className="itemc">
                <strong>Drivers</strong>
                <p>Half the Lambos have 11 different drivers! Maybe you’ll get lucky with one that’s just like you!</p>
              </div>
            </div>

            <div className="item">
              <div><img className="itemimg" src={img_itm3} alt="" /></div>
              <div className="itemc">
                <strong>Backgrounds</strong>
                <p>50 Stunning and unique pixel drawn backgrounds inspired from Geek and Crypto culture</p>
              </div>
            </div>          
        </section>

        <section className="seen">
          
            <div className="hl">As seen on</div>

            <div className="listinglinks">
              <a href="https://nftcalendar.io" ><div style={{backgroundImage: `url(${img_nftcalendar})`}} />nftcalendar.io</a>
              <a href="https://www.nftdroplist.co.uk" ><div style={{backgroundImage: `url(${img_nftdroplist})`}} />nftdroplist.co.uk</a>
              <a href="https://nextdrop.is" ><div style={{backgroundImage: `url(${img_nextdrop})`}} />nextdrop.is</a>
              <a href="https://www.non-fungi.com" ><div style={{backgroundImage: `url(${img_nonfungi})`}} />non-fungi.com</a>
            </div>
          
        </section>
      
        <img src={img_lineup} alt="" className="reflectionlineup" />
        
        <section className="details">
          <div>
            <div className="hl">Roadmap and Details</div>
            <p>
            We have all the up to date details on our discord!<br />
            We suggest heading over there to get the latest.
            </p>
            <p>
            Info about MarketPlace Listing, Exclusive Airdrops,<br />
            Floorsweeping, Collabs and Special Giveaways
            </p>

            
            <div className="somelinks">
              <a href="https://discord.gg/DuxthFgF6q"><div style={{backgroundImage: `url(${img_discord})`}} /></a>
              <a href="https://twitter.com/PixeLambo"><div style={{backgroundImage: `url(${img_twitter})`}} /></a>
            </div>
          </div>
        </section>


       

      </div>
      
      <section className="footer">
        @ 2021 Lambo Diamond Hands Club. All rights reserved.
      </section>


      {wallet && (
        <div className="wallet">
          <pre>
            Wallet {shortenAddress(wallet.publicKey.toBase58() || "")}<br />
            Balance: {(balance || 0).toLocaleString()} SOL<br />
            Total Available: {itemsAvailable}<br />
            Redeemed: {itemsRedeemed}<br />
            Remaining: {itemsRemaining}<br />
            Hashlist: {process.env.REACT_APP_HASHLIST}<br />
            Cache: {process.env.REACT_APP_CACHE}
          </pre>
        </div>
      )}

      <Snackbar
        open={alertState.open}
        autoHideDuration={6000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert
          onClose={() => setAlertState({ ...alertState, open: false })}
          severity={alertState.severity}
        >
          {alertState.message}
        </Alert>
      </Snackbar>

      </div>
      </div>

</div>
  );
};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

const renderCounter = ({ days, hours, minutes, seconds, completed }: any) => {
  // console.log(days, hours, minutes, seconds)
  return (
    <CounterText className="counter">
      {/* {hours + (days || 0) * 24} hours, {minutes} minutes, {seconds} seconds */}
      
      <div className="counterunit">{days || 0} days,</div>
      <div className="counterunit">{hours || 0} hours,</div>
      <div className="counterunit">{minutes} minutes,</div>
      <div className="counterunit">{seconds} seconds</div>

      {/* {days}:{hours}:{minutes}:{seconds} isSoldOut{`${isSoldOut}`} */}

    </CounterText>
  );
};

export default Home;
